import {
    mapState,
    mapMutations
} from 'vuex';
import store from "../../store/index.js";
import api from "../../api/api.js";
export default {
    computed: mapState(['hasLogin', 'userInfo']),
    data() {
        return {
            targetUserInfo: {},
            targetUserId: ''
        }
    },
    onShow(id) {
        // let {
        //     id
        // } = option
        // console.log(option.id)
        //id为点击头像传进来的id
        this.targetUserId = id
        var url = api.urls.getOtherUserInfo + id
        var data = {}
        var that = this
        api.req.get(url, data, (res) => {
            if (res.code === 0) {
                that.targetUserInfo = res.data
            } else {
                uni.showModal({
                    content: res.msg,
                    showCancel: false
                })
            }
        });
    },
    methods: {
        follow() {
            if (this.hasLogin) {
                if (!this.targetUserInfo.hasFollow) {
                    var that = this
                    var url = api.urls.attentionSomeone
                    var data = {
                        beAttentionUserId: that.targetUserId
                        // beAttentionUserId: 225
                    }
                    api.req.post(url, data, (res) => {
                        if (res.code === 0) {
                            that.targetUserInfo.followerNum = that.targetUserInfo.followerNum + 1
                            uni.showToast({
                                title: '关注成功',
                                icon: 'success',
                                duration: 1000
                            });
                            that.targetUserInfo.hasFollow = true
                        } else {
                            uni.showModal({
                                content: res.msg,
                                showCancel: false
                            })
                        }
                    });
                } else {
                    var that = this
                    var url = api.urls.cancelAttention
                    var data = {
                        beAttentionUserId: that.targetUserId
                        // beAttentionUserId: 225
                    }
                    api.req.del(url, data, (res) => {
                        if (res.code === 0) {
                            that.targetUserInfo.followerNum = that.targetUserInfo.followerNum - 1
                            uni.showToast({
                                title: '取关成功',
                                icon: 'success',
                                duration: 1000
                            });
                            that.targetUserInfo.hasFollow = false
                        } else {
                            uni.showModal({
                                content: res.msg,
                                showCancel: false
                            })
                        }
                    });
                }
            } else {
                uni.showModal({
                    content: "关注需要登录，您想马上登录吗？",
                    success: function(res) {
                        if (res.confirm) {
                            uni.navigateTo({
                                url: '../../pages/my/login/login'
                            })
                        } else if (res.cancel) {
                            return;
                        }
                    }
                });
            }
        },
        goChat() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: '../../pages/messages/message-detail/message-detail'
                })
            } else {
                uni.showModal({
                    content: "发消息需要登录，您想马上登录吗？",
                    success: function(res) {
                        if (res.confirm) {
                            uni.navigateTo({
                                url: '../../pages/my/login/login'
                            })
                        } else if (res.cancel) {
                            return;
                        }
                    }
                });
            }
        }
    }
}
